	module.exports = {"data":{"markdownRemark":{"html":"<h2>Security</h2>\n<p>Information Security (also called “infosec” or just “security”) deals with guarding and breaking into systems.</p>\n<p>This is a particularly important space right now, following major breaches such as the <a href='https://medium.freecodecamp.org/the-equifax-hack-and-how-to-protect-your-family-all-explained-in-5-minutes-a2b5187cb6c0' target='_blank' rel='nofollow'>2017 Equifax breach</a>.</p>\n<p>Information security practitions are often called “security researchers”, though they are also often referred to as “hackers” for hacking into systems.</p>\n<p>There are “white hat hackers” (ethical hackers) who try to discover new vulnerabilities and bring them to the attention of the people maintaining those systems. They often do this for prestige in the field, or for “bug bounties” that companies have set aside to compensate people for helping them discover vulnerabilities. </p>\n<p>In an interesting hack on Parity (on the Ethereum blockchain), “white hack hackers” exploited a vulnerability and drained all the wallets of money before a real hacker could exploit the same vulnerability (without returning funds): <a href=\"https://medium.freecodecamp.org/a-hacker-stole-31m-of-ether-how-it-happened-and-what-it-means-for-ethereum-9e5dc29e33ce\">https://medium.freecodecamp.org/a-hacker-stole-31m-of-ether-how-it-happened-and-what-it-means-for-ethereum-9e5dc29e33ce</a>. This led to a loss of $31 million, instead of a loss of over $180 million. </p>\n<p>There are also “black hat hackers” who do not disclose the vulnerabilities they discover, and instead use them to create exploits to attack people’s systems. They often do this to steal data that they can ransom off, or just release into the open to cause chaos.</p>\n<p>Security researchers use a wide variety of methods and tools, which are discussed in this section.</p>\n<p>Although hacking is a key threat to secure applications, it is not the only one.  Application developers need to balance user experience with security features.  Some cyber breches are self-infliced due to complex or hard to understadn security configurations.  What security features should be set by default?  What features should be seemless and which should require explict user consent? </p>","fields":{"slug":"/security/"},"frontmatter":{"title":"Security"}}},"pathContext":{"slug":"/security/"}}

